import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


//
import { ProyectoComponent } from './proyecto/proyecto.component';
import { Productos1Component } from './proyecto/productos1/productos1.component';
import { Productos1ProductosComponent } from './proyecto/productos1-productos/productos1-productos.component';
import { Productos2Component} from './proyecto/productos2/productos2.component';

//
import { LoginComponent } from './components/login/login.component';
import { RegistrarComponent } from './components/registrar/registrar.component';

//
import { ProyectoEntroComponent } from './proyecto-entro/proyecto-entro.component';
import { PrincipalComponent } from './proyecto-entro/principal/principal.component';

const APP_ROUTES: Routes = [
  { path: 'System', component: ProyectoComponent, children: [
      { path: 'Productos1', component: Productos1Component },
      { path: 'Productos1/Producto/:id', component: Productos1ProductosComponent },
      { path: 'Productos2', component: Productos2Component },
      { path: '**', pathMatch: 'full', redirectTo: 'Productos1' }
    ]},
  
    { path: 'login', component: LoginComponent },
    { path: 'Registro', component: RegistrarComponent },
    { path: 'SystemProductos',  component: ProyectoEntroComponent, children: [
      { path: 'Principal', component: PrincipalComponent },
      { path: '**', pathMatch: 'full', redirectTo: 'Principal' }
    ]},
    { path: '**', pathMatch: 'full', redirectTo: 'System' }
];

export const APP_ROUTING = RouterModule.forRoot(APP_ROUTES);
