import { Component } from '@angular/core';


import { ActivatedRoute } from '@angular/router';
import { SchoolsService } from '../servicio/school.service';


@Component({
  selector: 'app-productos1-productos',
  templateUrl: './productos1-productos.component.html',
  styleUrls: ['./productos1-productos.component.css']
})
export class Productos1ProductosComponent  {
  producto: any = [];

  constructor( private activatedRoute: ActivatedRoute, private _schoolsService: SchoolsService ) {
    this.activatedRoute.params.subscribe( params => {
      this.producto = this._schoolsService.getProducto1(params['id']);
    });
   }


}
