import { Component, OnInit } from '@angular/core';
import { SchoolsService, Producto1 } from '../servicio/school.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-productos1',
  templateUrl: './productos1.component.html',
  styleUrls: ['./productos1.component.css']
})
export class Productos1Component implements OnInit {


  producto1: Producto1[] = [];

  constructor( private _schoolsService: SchoolsService, private router: Router ) { }

  ngOnInit() {
    this.producto1 = this._schoolsService.getProductos1();
  }

  verProducto( idx: number ) {
    this.router.navigate(['System/Productos1/Producto', idx]);

  }

}
